
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

export default {
name: "fp",
props: ["id"],
data: function() {
return {
cityname: null,
cityid: null,
info: null,
list: [],
inList: []
};
},
components: {
Panel,
Group,
XInput,
XButton,
Flexbox,
FlexboxItem,
CellBox,
Checklist
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {

vm.$http.get("/location-client.html?").then(function(response) {
vm.cityid = response.data.data.CityID;

vm.cityname = response.data.data.CityName;
vm.$http
.get(

"/v2-dealer-alllist.html?carId=" + vm.id + "&cityId=" + vm.cityid)
.then(function(response) {

vm.info = response.data.data;
for (var i = 0; i < vm.info.list.length; i++) {
var obj = [{}];
obj[0].key = i + 1;
obj[0].value = vm.info.list[i].dealerShortName;
obj[0].inlineDesc = vm.info.list[i].address;
vm.inList.push(obj);
}

vm.list = [
{
title: vm.info.details.serial.AliasName,
desc:
vm.info.details.market_attribute.year +""+vm.info.details.car_name,
src: vm.info.details.serial.Picture
}
];
});
});
});
}
};



