export default {
  name: "img",
  props: ["id"],
  data: function() {
    return {
      wg: {},
      ns: {},
      kj: {},
      tj: {},
      gf: {}
    };
  },
  methods: {
    picture(aid, bid, cid) {
      // console.log(aid)
      // console.log(bid)
      // console.log(cid)
      this.$router.push("/imgmore/" + aid + "/" + bid);
    }
  },
  beforeRouteEnter: function(to, from, next) {
    next(function(vm) {
      vm.$http
        .get("/v2-car-getImageList.html?SerialID=" + to.params.id)
        .then(function(response) {
          console.log(response.data.data);
          vm.wg = response.data.data[0];
          vm.ns = response.data.data[1];
          vm.kj = response.data.data[2];
          vm.tj = response.data.data[3];
          vm.gf = response.data.data[4];
          // console.log(vm.ns)
        });
    });
  }
};



