
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name: "home",
components: {
Group,
Cell,
Popup
},
data: function() {
return {
isShowPopup: false,
key_MasterID: null,
key_SerialID: null,
dpage: [],
lists: [],
cars: [
{
title: "A",
list: []
},
{
title: "B",
list: []
},
{
title: "C",
list: []
},
{
title: "D",
list: []
},
{
title: "F",
list: []
},
{
