list: []
},
{
title: "L",
list: []
},
{
title: "M",
list: []
},
{
title: "N",
list: []
},
{
title: "O",
list: []
},
{
title: "Q",
list: []
},
{
title: "R",
list: []
},
{
title: "S",
list: []
},
{
title: "T",
list: []
},
{
title: "W",
list: []
},
{
title: "X",
list: []
},
{
title: "Y",
list: []
},
{
title: "Z",
list: []
}
]
};
},
methods: {

hidepopup: function() {
this.isShowPopup = false;
this.key_MasterID = null;
},

showPopup: function(id) {
this.isShowPopup = true;
this.key_MasterID = id;
var app = this;
this.$http
.get("/v2-car-getMakeListByMasterBrandId.html?MasterID=" + id)
.then(function(response) {

app.dpage = response.data.data;
app.id = response.data.SerialID;
});
}
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {

vm.$http.get("/v2-car-getMasterBrandList.html").then(function(response) {

vm.lists = response.data.data;
for (var i = 0; i <= vm.lists.length; i++) {
if (vm.lists[i].Spelling.slice(0, 1) == "A") {
vm.cars[0].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "B") {
vm.cars[1].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "C") {
vm.cars[2].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "D") {
vm.cars[3].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "F") {
vm.cars[4].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "G") {
vm.cars[5].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "H") {
