}
if (vm.lists[i].Spelling.slice(0, 1) == "Q") {
vm.cars[13].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "R") {
vm.cars[14].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "S") {
vm.cars[15].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "T") {
vm.cars[16].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "W") {
vm.cars[17].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "X") {
vm.cars[18].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "Y") {
vm.cars[19].list.push(vm.lists[i]);
}
if (vm.lists[i].Spelling.slice(0, 1) == "Z") {
vm.cars[20].list.push(vm.lists[i]);
}
}
});
});
}
};



