
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name: "imgmore",
props: ["aid", "bid"],
data: function() {
return {
imgmore: {},
page: 1,
pullupConfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: ""
}
};
},
components: {
Scroller
},
methods: {
loadMoreData: function() {
var app = this;
this.page = this.page + 1;
this.$http
.get(
"/v2-car-getCategoryImageList.html?SerialID=" +
this.aid +
"&ImageID=" +
this.bid +
"&Page=" +
this.page +
"&PageSize=30&_1531054352382"
)
.then(function(response) {

for (var i = 0; i < response.data.data.List.length; i++) {
app.imgmore.List.push(response.data.data.List[i]);
}
app.$refs.pullup.donePullup();
});
}
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http
.get(
"/v2-car-getCategoryImageList.html?SerialID=" +
to.params.aid +
"&ImageID=" +
to.params.bid +
"&Page=" +
vm.page +
"&PageSize=30&_1531054352382"
)
.then(function(response) {

vm.imgmore = response.data.data;
});
});
}
};



