import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/components/Home.vue';
import Topic from '@/components/Topic.vue';
import Img from '@/components/Img.vue';
import Imgmore from '@/components/Imgmore.vue';
import FP from '@/components/FP.vue';
//
Vue.use(VueRouter)
    //
const routes = [{
        path: '/',
        redirect: '/home'
    },
    {
        path: '/home',
        name: 'home',
        component: Home
    },
    {
        path: '/topic/:id',
        name: 'topic',
        component: Topic,
        props: true
    },
    {
        path: '/img/:id',
        name: 'img',
        component: Img,
        props: true
    },
    {
        path: '/imgmore/:aid/:bid',
        name: 'imgmore',
        component: Imgmore,
        props: true
    },
    { path: '/fp/:id', name: 'fp', component: FP, props: true }

];

//
const router = new VueRouter({
    routes
});
//,
export default router;


