
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name: "topic",
props: ["id"],
components: {
Group,
Cell,
XButton,
Tab,
TabItem
},
data() {
return {
list: [],
market: [],
lists: [],
sid: null
};
},
methods: {
djsj: function(id) {
var app = this;
this.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + id)
.then(function(response) {
app.list = response.data.data.list;
});
},
djsj1: function(id) {
var app = this;
this.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + id)
.then(function(response) {
var listtest = [];
for (var i = 0; i < response.data.data.list.length; i++) {
if (response.data.data.list[i].market_attribute.year == "2019") {
listtest.push(response.data.data.list[i]);
}
}
app.list = listtest;
});
},
djsj2: function(id) {
var app = this;
this.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + id)
.then(function(response) {
var listtest = [];
for (var i = 0; i < response.data.data.list.length; i++) {
if (response.data.data.list[i].market_attribute.year == "2018") {
listtest.push(response.data.data.list[i]);
}
}
app.list = listtest;
});
}
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id)
.then(function(response) {



vm.market = response.data.data.market_attribute;
vm.list = response.data.data.list;
vm.lists = response.data.data;
vm.sid = response.data.data.SerialID;
});
});
}
};



